export default {
	created() {
		uni.removeStorageSync('userinfo')
	},
	methods: {
		bindStudent() {
			const openid = this.$route.query && this.$route.query.openid;
			uni.redirectTo({
				url: '../../pages/index/index?openid=' + openid
			})
		},
		bindTeacher() {
			const openid = this.$route.query && this.$route.query.openid;
			uni.redirectTo({
				url: '../../pages/index/teacher-bind?openid=' + openid
			})
		}
	}
}
